                <p>{{ messageSchema }}</p>
                <div class="boxes">
                    <div class="box style-schema" v-for="schema in schemasToShow" @click="goto(schema)">
                            <div class="box-header">
                                {{ schema.title }}
                            </div>
                            <div class="box-logo">
                                <img src="../static/images/diagram_grey.png" width="30" height="30"/>
                            </div>
                            <div class="box-content">{{ schema.description }}</div>
                    </div>
